import React from 'react';
import {
  Card, CardSubtitle, CardTitle,
  CardBody, MobileCard, MobileCardContainer
} from '../atoms/Card';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Table } from './table'
import { IBike, IBikeStore } from '../../lib/stores/bike';
import { IconButton, StyledButton } from '../atoms/Button';
import TableInput from './TableInput';

interface Props {
  bikeStore: IBikeStore;
}

export const MobileBikeList = ({bikeStore, router }: any) => {
  return (
    <Card>
      <CardBody>
        <CardTitle>My Bikes</CardTitle>
        <CardSubtitle>Select bike to manage components</CardSubtitle>
        <MobileCardContainer>
          {
            [
              ...bikeStore.list.map((bike: IBike) =>
                bikeStore.edits.has(bike.id) ? bikeStore.edits.get(bike.id) : bike
              ),
              ...bikeStore.newBikes
            ].map((bike: any) => {
              return (
                <MobileCard>
                  <CardBody>
                    <CardTitle>{bike.name}</CardTitle>
                  </CardBody>
                </MobileCard>
              )
            })
          }
        </MobileCardContainer>
      </CardBody>
    </Card>
  )
};

const BikeList = ({ bikeStore }: Props) => {
  const setCB = (id: string, field: string): any => {
    // let timeout: any = null;
    return (val: string) => {
      bikeStore.updateBike(id, field, val)
      // if (timeout) {
      //   clearTimeout(timeout);
      // }
      // timeout = setTimeout(() => {
      //   bikeStore.updateBike(id, field, val);
      // }, 500);
    }
  };

  return (
    <Card>
      <CardBody>
        <CardTitle>My Bikes</CardTitle>
        <CardSubtitle>Select bike to manage components</CardSubtitle>
        <Table>
          <thead>
            <tr>
              <th>#</th>
              <th>Name</th>
              <th>Brand</th>
              <th>Model</th>
              <th>Distance</th>
              <th>Elevation</th>
            </tr>
          </thead>
          <tbody>
          {
            [
              ...bikeStore.list.map((bike: IBike) =>
                bikeStore.edits.has(bike.id) ? bikeStore.edits.get(bike.id) : bike
              ),
              ...bikeStore.newBikes
            ].map((bike: any, index: number) => {
                return (
                  <tr
                    onClick={() => bikeStore.selectBike(bike.id)}
                    key={bike.id}
                    className={bikeStore.selectedBikeId === bike.id ? 'selected' : ''}
                  >
                    <th scope="row">{index + 1}</th>
                    <TableInput val={bike.name} cb={setCB(bike.id, 'name')} />
                    <TableInput val={bike.brand} cb={setCB(bike.id, 'brand')}/>
                    <TableInput val={bike.model} cb={setCB(bike.id, 'model')}/>
                    <td>{bike.distance}</td>
                    <td>{bike.elevation}</td>
                    </tr>
                )
              })
          }
          </tbody>
          <tfoot>
          <tr>
            <td>
              <IconButton onClick={() => bikeStore.insertNewBike()}>
                <FontAwesomeIcon icon="plus"/>
              </IconButton>
            </td>
            {
              !!bikeStore.edits.size && (
                <td colSpan={2}>
                  <StyledButton width="auto" style={{'marginRight': 5}}>
                    <FontAwesomeIcon icon="check" /> Save
                  </StyledButton>
                  <StyledButton
                    onClick={() => bikeStore.discardChanges()}
                    width="auto" style={{'marginRight': -5}}
                  >
                    <FontAwesomeIcon icon="times" /> Discard
                  </StyledButton>
                </td>
              )
            }
          </tr>
          </tfoot>
        </Table>
      </CardBody>
    </Card>
  );
};

export default BikeList;
