import axios,
{ 
  AxiosInstance,
  AxiosRequestConfig,
  AxiosResponse,
} from 'axios';

class BaseApi {
  apiInstance: AxiosInstance;

  constructor(baseURL: string, configHeaders?: any) {
    this.apiInstance = axios.create({
      baseURL,
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
        ...configHeaders,
      }
    });
  }

  protected get = async (url: string, config?: AxiosRequestConfig): Promise<AxiosResponse> => {
    return this.apiInstance.get(url, config);
  };

  protected post = async (url: string, data?: any, config?: AxiosRequestConfig): Promise<AxiosResponse> => {
    return this.apiInstance.post(url, data, config);
  };

  protected delete = async (url: string, config?: AxiosRequestConfig): Promise<AxiosResponse> => {
    return this.apiInstance.delete(url, config);
  };
}

export default BaseApi;