import styled from 'styled-components';
import theme from '../../lib/styles/theme';

type Props = {
  width?: string;
  height?: string;
  color?: string;
  backgroundColor?: string;
  selected?: boolean;
}

const BaseButton = styled.button`
  width: auto;
  font-weight: 400;
  font-size: 1rem;
  background-color: ${theme.colors.cyanGreyDark};
  color: ${theme.colors.bluePale};
  text-align: center;
  vertical-align: middle;
  line-height: 1.5px;
  transition:
    color .15s ease-in-out,
    background-color .15s ease-in-out,
    border-color .15s ease-in-out,
    box-shadow .15s ease-in-out,
    -webkit-box-shadow .15s ease-in-out;

  &:hover {
    background-color: ${theme.colors.cyanBlueGrey};
  }

  &:focus {
    background-color: ${theme.colors.cyanBlueGrey};
  }

  &:disabled {
    cursor: not-allowed;
  }
`;

export const StyledButton = styled(BaseButton)`
  width: ${(props: Props) => (props.width || 'auto')};
  height: ${(props: Props) => (props.height || '40px')};
  border-radius: .3rem;
  border: none;
  padding: .5rem 1rem;
  svg {
    margin-right: 5px;
    font-size: .75rem;
  }
  
  .selected {
    background-color: ${theme.colors.cyanBlueGrey};
  }
`;

export const IconButton = styled(BaseButton)`
   border-radius: 50%;
    width: 40px;
    height: 40px;
    padding: 0 0 1px 0;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border: 1px solid transparent;
`;

export const StyledButtonWrapper = styled.div`
  width: max-content;
  height: max-content;
  background-color: ${theme.colors.cyanGreyDark};
  border: none;
  color: ${(props: Props) => (props.color || theme.colors.bluePale)};
  font-size: 1rem;
  text-transform: uppercase;
  font-weight: 400;
  display: flex;
  justify-content: center;
  align-items: center;
  
  &:hover {
    background-color: ${theme.colors.cyanBlueGrey};
    cursor: pointer;
  }

  &:focus {
    background-color: ${theme.colors.cyanBlueGrey};
  }

  &:disabled {
    cursor: not-allowed;
  }
`;
