import React from 'react';
import ReactDOM from 'react-dom';
import { Router } from 'react-router';
import { createBrowserHistory } from 'history';
import { Provider } from 'mobx-react';
import { GlobalStyle } from './lib/styles/global';
import { syncHistoryWithStore } from 'mst-react-router';
import { library } from '@fortawesome/fontawesome-svg-core'
import { fab } from '@fortawesome/free-brands-svg-icons'
import { faBars, faBicycle, faCogs, faPlus, faCheck, faTimes } from '@fortawesome/free-solid-svg-icons'

import App from './components/App';
import * as serviceWorker from './serviceWorker';
import RootStore from './lib/stores/rootStore';
import GearTrackerApi from './agents/gt-api';
import { onSnapshot } from 'mobx-state-tree';

library.add(fab, faBars, faBicycle, faCogs, faPlus, faCheck, faTimes);
let INITIAL_STATE = {};

if(localStorage.getItem("gearTracker")) {
  const json = JSON.parse((localStorage.getItem("gearTracker") || ''));
  if(RootStore.is(json)) {
    INITIAL_STATE = json;
  }
}

const store = RootStore.create(INITIAL_STATE);

onSnapshot(
  store,
  snapshot => localStorage.setItem("gearTracker", JSON.stringify(snapshot)),
);

const history = syncHistoryWithStore(createBrowserHistory(), store.router);

GearTrackerApi.createInstance(store.session);

ReactDOM.render((
  <Provider {...store}>
    <Router history={history}>
      <React.Fragment>
        <GlobalStyle />
        <App />
      </React.Fragment>
    </Router>
  </Provider>
  ), document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: http://bit.ly/CRA-PWA
serviceWorker.unregister();
