import { types, Instance, flow } from 'mobx-state-tree';
import GearTrackerApi from '../../agents/gt-api';
import uuid from 'uuid';

export const Bike = types
  .model('Bike', {
    id: types.string,
    created_date: types.optional(types.string, ''),
    modified_date: types.optional(types.string, ''),
    name: types.optional(types.string, ''),
    brand: types.optional(types.string, ''),
    model: types.optional(types.string, ''),
    distance: types.optional(types.number, 0),
    elevation: types.optional(types.number, 0),
  }).actions(self => ({
    setName: (name: string) => self.name = name
  }));

const BikeStore = types
  .model('BikeStore', {
    bikes: types.optional(types.map(Bike), {}),
    edits: types.optional(types.map(Bike), {}),
    selectedBikeId: types.optional(types.string, ''),
  })
  .actions(self => {
    // @ts-ignore
    const fetchBikes = flow(function* () {
      try {
        const bikes: IBike[] = yield GearTrackerApi.getBikes();
        self.bikes.clear();
        bikes.forEach(bike => {
          self.bikes.set(bike.id, bike);
        });
      } catch (e) {
        console.log('Error fetching bikes', e);
      }
    });

    // @ts-ignore
    const addBike = flow(function* (bike: IBike) {
      try {
        const createdBike: IBike = yield GearTrackerApi.addBike(bike);
        self.bikes.set(createdBike.id, createdBike);
      } catch (e) {
        console.log(e);
      }
    });

    const insertNewBike = (): void => {
      const id = uuid();
      self.selectedBikeId = id;
      self.edits.set(id, {
        id,
        created_date: new Date().toISOString().split('T')[0],
        modified_date: new Date().toISOString().split('T')[0],
        name: '',
        brand: '',
        model: '',
        distance: 0,
        elevation: 0,
      });
    };

    const updateBike = (id: string, field: string, value: string): void => {
      console.log('id: ', id);
      console.log('field: ', field);
      console.log('value: ', value);
      const originalBike: any = self.bikes.get(id);
      let editBike: any = self.edits.get(id);
      if (originalBike && editBike) {
        editBike[field] = value;
        if (JSON.stringify(originalBike.toJSON()) === JSON.stringify(editBike.toJSON())) {
          self.edits.delete(id);
        } else {
          self.edits.set(id, editBike);
        }
      } else if (originalBike) {
        editBike = originalBike.toJSON();
        editBike[field] = value;
        self.edits.set(id, editBike);
      } else if (editBike) {
        console.log('yep', editBike)
        editBike.setName(value);
        self.edits.set(id, editBike);
      }
    };

    const selectBike = (id: string): void => {
      self.selectedBikeId = id;
    };

    const discardChanges = (): void => {
      self.edits.clear();
    };

    return {
      updateBike,
      fetchBikes,
      addBike,
      insertNewBike,
      discardChanges,
      selectBike,
    }
  })
  .views(self => ({
    get list() {
      return Array.from(self.bikes.values());
    },
    get newBikes() {
      return Array.from(self.edits.values()).filter(bike => isNaN(+bike.id));
    },
    get selectedBike() {
      const selectedId = self.selectedBikeId;
      if (self.edits.has(selectedId)) {
        // @ts-ignore
        return self.edits.get(selectedId);
      }
      if (self.bikes.has(selectedId)) {
        // @ts-ignore
        return self.bikes.get(selectedId);
      }
      return {
        id: '',
        created_date: new Date().toISOString().split('T')[0],
        modified_date: new Date().toISOString().split('T')[0],
        name: '',
        brand: '',
        model: '',
        distance: 0,
        elevation: 0,
      }
    },
  }));

export interface IBike extends Instance<typeof Bike> {}
export interface IBikeStore extends Instance<typeof BikeStore> {}

export default BikeStore;
