import { types, Instance } from 'mobx-state-tree';
import { RouterModel } from 'mst-react-router';

import Session from './session';
import BikeStore from './bike';
import Gear from './gear';
import Activity from './activity';

const RootStore = types.model('RootStore', {
  session: types.optional(Session, {}),
  router: types.optional(RouterModel, {}),
  bikes: types.optional(BikeStore, {}),
  gear: types.optional(types.array(Gear), []),
  activities: types.optional(types.array(Activity), []),
});

export interface IRootStore extends Instance<typeof RootStore> {}
export interface IRouter extends Instance<typeof RouterModel> {}

export default RootStore;
