import { types, Instance} from 'mobx-state-tree';

const Activity = types
.model('Activity', {
  id: types.string,
  created_date: types.maybe(types.Date),
  modified_date: types.maybe(types.Date),
  title: types.optional(types.string, ''),
  description: types.optional(types.string, ''),
  distance: types.optional(types.number, 0),
  elevation: types.optional(types.number, 0),
});

export interface IActivity extends Instance<typeof Activity> {}

export default Activity;
