import React from 'react'
import { inject, observer } from 'mobx-react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { Container, List, StyledLink } from './styles';

const SideNav = ({session, router}: any) => {
  const path = router.location.pathname;
  // @ts-ignore
  window.mobRouter = router;

  return (
    <Container>
      <List>
        {
          !!session.id && (
            <li>
              <StyledLink to="/bikes" selected={path === "/bikes"}>
                <FontAwesomeIcon icon="bicycle"/> Bikes
              </StyledLink>
            </li>
          )
        }
        {
          !!session.id && (
            <li>
              <StyledLink to="/components" selected={path === "/components"}>
                <FontAwesomeIcon icon="cogs"/> Components
              </StyledLink>
            </li>
          )
        }
      </List>
    </Container>
  )

};

export default inject('session', 'router')(observer(SideNav));
