import { AxiosError } from 'axios';
import BaseApi from './base-api';
import { ISession } from '../lib/stores/session';
import { IBike } from '../lib/stores/bike';

const API_ROOT = process.env.REACT_APP_GT_API || '';

class GearTrackerApi extends BaseApi {
  private static apiInstance: GearTrackerApi;

  static createInstance = (auth: ISession): void => {
    GearTrackerApi.apiInstance = new GearTrackerApi(auth);
  };

  private constructor(private session: ISession) {
    super(API_ROOT, {
     "Access-Control-Allow-Origin": "*"
    });
  }

  private handleResponseError = (err: AxiosError): AxiosError => {
    if(
      this.session.id &&
      err && err.response &&
      err.response.status === 401
      ) {
      this.session.clearSession();
    }
    return err;
  };

  static addBike = async (bike: IBike): Promise<IBike> => {
    try {
      const { data } = await GearTrackerApi.apiInstance.post('/bike', bike);
      return data;
    } catch (e) {
      GearTrackerApi.apiInstance.handleResponseError(e);
      throw e;
    }
  };

  static getBikes = async (): Promise<IBike[]> => {
    try {
      // const { data }: { data: IBike[] } = await GearTrackerApi.apiInstance.get('/bike');
      // @ts-ignore
      return bikes.map(bike => {
        bike.id = `${bike.id}`;
        bike.created_date = bike.created_date.split('T')[0];
        bike.modified_date = bike.modified_date.split('T')[0];
        return bike;
      });
    } catch (e) {
      GearTrackerApi.apiInstance.handleResponseError(e);
      throw e;
    }
  };
}

