import { types, Instance} from 'mobx-state-tree';

const Gear = types
  .model('Gear', {
    id: types.string,
    created_date: types.maybe(types.Date),
    modified_date: types.maybe(types.Date),
    name: types.optional(types.string, ''),
    brand: types.optional(types.string, ''),
    model: types.optional(types.string, ''),
    distance: types.optional(types.number, 0),
    elevation: types.optional(types.number, 0),
    date_installed: types.maybe(types.Date),
    date_removed: types.maybe(types.Date),
    bike: types.number,
  });

