import React from 'react';
import { Card, CardSubtitle, CardTitle, CardBody } from '../atoms/Card'
import { Table } from './table'

const BikeComponents = ({ components, bike }: any) => {

  return (
    <Card>
      <CardBody>
        <CardTitle>{bike.name? bike.name + "'s Components" : ''}</CardTitle>
        <CardSubtitle>Managing Components Subtitle</CardSubtitle>
        <Table>
          <thead>
            <tr>
              <th>#</th>
              <th>Name</th>
              <th>Type</th>
              <th>Brand</th>
              <th>Model</th>
              <th>Distance</th>
              <th>Elevation</th>
            </tr>
          </thead>
          <tbody>
            {
              components
                .filter((component: any) => component.bike === +bike.id)
                .map((component: any, index: number) => {
                  return (
                    <tr key={component.id}>
                      <th scope="row">{index + 1}</th>
                      <td>{component.name}</td>
                      <td>{component.type}</td>
                      <td>{component.brand}</td>
                      <td>{component.model}</td>
                      <td>{component.distance}</td>
                      <td>{component.elevation}</td>
                    </tr>
                  )
                })
            }
          </tbody>
        </Table>
      </CardBody>
    </Card>
  )
};

export default BikeComponents;

