import React, { Component } from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';
import { inject, observer } from 'mobx-react';
import styled from 'styled-components';
import Header from './organisms/Header';
import SideNav from './organisms/SideNav';
import Authentication from '../components/organisms/Authentication';
import Bikes from '../components/organisms/Bikes'

const AppContainer = styled.div`
  position: relative;
`;

const Content = styled.section`
  margin-top: -144px;
  
  @media (max-width: 767.98px) {
    padding: 0 1rem 2rem;
  }
  @media (max-width: 1199.98px) and (min-width: 768px) {
    padding: 0 2rem 2rem;
  }
  @media (min-width: 1200px) {
    padding: 0 3rem 3rem 20rem;
  }
`;

const PrivateRoute = inject('session')(observer(({ component: Component, ...rest }: any) => {
  const { session = { id: 0 }} = {...rest} || {};
  return (
    <Route
      {...rest}
      render={(props: any) =>
        session.id ? (
          <Component {...props} />
        ) : (
          <Redirect
            to={{
              pathname: "/login",
              state: { from: props.location }
            }}
          />
        )
      }
    />
  );
}));

class App extends Component<any> {
  render() {
    return (
      <>
        <Header/>
        <AppContainer>
          <SideNav />
          <Content>
            <Switch>
              <Route exact path='/login' component={Authentication} />
              <PrivateRoute exact path="/" component={Bikes} />
              <PrivateRoute exact path="/bikes" component={Bikes} />
            </Switch>
          </Content>
        </AppContainer>
      </>
    );
  }
}

export default App;


