import BaseApi from './base-api';
import { ISession } from '../lib/stores/session';
import { IAuthInfo, oAuthInfo } from '../components/organisms/Authentication';

const API_ROOT = process.env.REACT_APP_AUTH_API || '';

class AuthApi extends BaseApi {
  private static apiInstance: AuthApi;

  static createInstance = (): void => {
    AuthApi.apiInstance = new AuthApi();
  };

  private constructor() {
    super(API_ROOT, {
      "Access-Control-Allow-Origin": "*"
    });
  }

  // @ts-ignore
  static authenticateUser = async (loginInfo: IAuthInfo | oAuthInfo, newUser: boolean = false): Promise<ISession> => {
    try {
      const { data } =
        await AuthApi.apiInstance.post(newUser ? '/register' : '/login', { user: loginInfo });
      return data;
    } catch (e) {
      throw e;
    }
  };
}

AuthApi.createInstance();

export default AuthApi;
