import { types, Instance, flow, getParent } from 'mobx-state-tree';
import { IAuthInfo, oAuthInfo } from '../../components/organisms/Authentication';
import AuthApi from '../../agents/auth-api';

// @ts-ignore
const Session = types
.model('Session', {
  email: types.optional(types.string, ''),
  id: types.optional(types.number, 0),
  imageUrl: types.optional(types.string, ''),
  name: types.optional(types.string, ''),
  firstName: types.optional(types.string, ''),
  lastName: types.optional(types.string, ''),
  apiKey: types.optional(types.string, ''),
})
.actions(self => {
  // @ts-ignore
  const authenticate = flow(function* (loginInfo: IAuthInfo | oAuthInfo, newUser: boolean = false) {
    try {
      const session: any = yield AuthApi.authenticateUser(loginInfo, newUser);
      self.email = session.email;
      self.id = session.id;
      self.imageUrl = session.image_url;
      self.name = session.username;
      self.firstName = session.first_name;
      self.lastName = session.last_name;
      self.apiKey = session.apiKey;
      const parent = getParent(self);
      yield parent.bikes.fetchBikes();
      parent.router.push('/bikes');
    } catch (e) {
      alert('Invalid Login');
      console.log(e);
      throw e;
    }
  });
  const clearSession = () => {
    self.email = '';
    self.id = 0;
    self.imageUrl = '';
    self.name = '';
    self.firstName = '';
    self.lastName = '';
    self.apiKey = '';
  };

  return {
    authenticate,
    clearSession
  }
});

export interface ISession extends Instance<typeof Session> {}

export default Session;
